.map{width:100%;display:flex;justify-content:center;position:relative;transition:all .1s ease;border-radius:none;box-shadow:rgba(0,0,0,.4) 0px 2px 4px,rgba(0,0,0,.3) 0px 7px 13px -3px,rgba(0,0,0,.2) 0px -3px 0px inset;border:1px solid rgba(0,0,0,.3);margin:20px 0 40px 0}.map::after{content:"";position:absolute;background:none;transition:all .1s ease;z-index:0;width:0%;height:0%}.map.disactive{transition:all .1s ease}.map.disactive::after{content:"Необходим подбор АБС";position:absolute;width:100%;height:100%;background:rgba(74,206,175,.7);z-index:10000;display:flex;justify-content:center;align-items:center;color:#232323;font-size:2rem;text-shadow:2px 2px 10px #fff;transition:all .1s ease}